.laurel {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 3rem;
}

.award {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    min-width: 230px;
    margin: 1rem;

    &__content {
        display: flex;
        flex-direction: column;
        margin-left: 1rem;
    }

    img {
        width: 50px;
        height: 50px;    
    }
}

@media screen and (min-width: 1900px) {
    .award {
        min-width: 390px;
    }
}

@media screen and (max-width: 450px) {
    .award {
        min-width: 100%;
        margin: 1rem 0;
    }
}